<!--  -->
<template>
  <div class="main">
    <!--轮播图-->
    <el-carousel class="swiper-container" :interval="4000" :height="swiperHeight + 'px'">
      <el-carousel-item v-for="(item, index) in swiperList" :key="index">
        <a :href="item.url"><img :src="item.pic" /></a>
      </el-carousel-item>
    </el-carousel>
    <!-- 排行榜 -->
    <div class="index_ranklist">
      <div class="rec_nav">
        <h3 class="title">排行榜</h3>
        <router-link to="/rankList" class="more">
          <span>更多</span>
          <i class="iconfont icon-icon_pagedown_"></i>
        </router-link>
      </div>
      <div class="rec_list">
        <template v-for="(item,index) in bangList">
          <div class="bang" :key="index">
            <div class="bang_top">
              {{item.name}}
            </div>
            <ul class="list_con">
              <template v-for="(item,index) in item.musicList.slice(0,5)">
                <li :key="index">
                  <div class="top_img " :class="'top'+(index+1)" v-if="index<=2"></div>
                  <div class="index" v-else>{{index+1}}</div>
                  <div class="info">
                    <p :title="item.name" class="song_name" @click="goPage('music',item.rid)">{{item.name}}</p>
                    <p :title="item.artist" class="artist" @click="goPage('singer',item.artistid)">{{item.artist}}</p>
                  </div>
                </li>
              </template>
            </ul>
          </div>
        </template>
      </div>
    </div>
    <!-- 歌单 -->
    <div class="index_playlist">
      <div class="rec_nav">
        <h3 class="title">推荐歌单</h3>
        <template v-for="(item,index) in playlistTags">
          <a href="javascript:;" :key="index" :class="{active:isActive===index}" @click="isActive=index; getPlaylist(item.id)">{{item.name}}</a>
        </template>
        <router-link to="/playlists" class="more">
          <span>更多</span>
          <i class="iconfont icon-icon_pagedown_"></i>
        </router-link>
      </div>
      <playlist :playlistData="playlistData"></playlist>
    </div>
    <!-- 歌手 -->
    <div class="index_singer">
      <div class="rec_nav">
        <h3 class="title">推荐歌手</h3>
        <template v-for="(item,index) in singerTags">
          <a href="javascript:;" :key="index" :class="{active:singerIsActive===index}" @click="singerIsActive=index;getSingerList(item.id)">{{item.name}}</a>
        </template>
        <router-link to="/singers" class="more">
          <span>更多</span>
          <i class="iconfont icon-icon_pagedown_"></i>
        </router-link>
      </div>
      <singer-list :artistData="artistData"></singer-list>
    </div>
  </div>
</template>

<script>
import { HttpManager } from '../api/index'
import { Carousel, CarouselItem } from 'element-ui';
import mixin from '../mixins'
import SingerList from '../components/SingerList.vue';
import Playlist from '../components/Playlist.vue';

export default {
  name: "MusicIndex",
  mixins: [mixin],
  data () {
    return {
      swiperHeight: 340,
      swiperWidth: 750,
      swiperList: [],
      pageUrl: { bang: "/rankList", playlists: "/playlists", singers: "/singers" },
      playlistTags: [],
      singerTags: [],
      bangList: [],
      // playlist: [],
      playlistData: {},
      // singerList: [],
      artistData: {},
      isActive: 0,
      singerIsActive: 0,
    };
  },

  components: {
    'el-carousel': Carousel,
    'el-carousel-item': CarouselItem,
    SingerList,
    Playlist
  },

  methods: {
    //获取每日推荐歌单
    getRcmPlaylist () {
      var that = this;
      HttpManager.getIndexRcmPlaylist()
        .then((res) => {
          that.playlistData = Object.assign({}, that.playlistData,
            {
              code: parseInt(res.code),
              total: 0,
              playlist: res.data.list.slice(0, 5),
              pageSize: 5,
              getData: null,
            })
          // that.playlist = res.data.list.slice(0, 5);//取前5项
        })
        .catch((error) =>{
          console.log(error);
        });
    },
    //获取首页歌单和歌手标签
    getTags () {
      var that = this;
      //获取歌单标签
      HttpManager.getIndexTagPlaylist()
        .then((res) => {
          // console.log(res.data.data);
          that.playlistTags = res.data;
          that.playlistTags.unshift({ name: "每日推荐", id: "rcm" })
        })
        .catch((error)=> {
          console.log(error);
        });
      //获取歌手标签
      HttpManager.getIndexTagSinger()
        .then((res) => {
          // console.log(res.data.data);
          that.singerTags = res.data;
        })
        .catch((error)=> {
          console.log(error);
        });
    },
    //获取首页数据
    getHomeData () {
      var that = this;

      this.getSwiperList();
      this.getTags();
      this.getRcmPlaylist();
      //获取华语歌手数据
      this.getSingerList(11);
      //获取排行榜数据
      HttpManager.getIndexBangList()
        .then((res) => {
          that.bangList = res.data;
        })
        .catch((error) =>{
          console.log(error);
        });

      console.log(document.cookie.split("kw_token=")[1]);
    },
    //获取指定类型的歌单
    getPlaylist (id) {
      let that = this;
      if (id == 'rcm') {
        this.getRcmPlaylist()
      }
      else {

        HttpManager.getPlaylist({ id, pn: 1, rn: 5 })
          .then((res) => {
            // console.log(res.data.data);
            that.playlistData = Object.assign({}, that.playlistData,
              {
                code: parseInt(res.code),
                total: 0,
                playlist: res.data.data,
                pageSize: 5,
                getData: null,
              })
            // that.dataList = res.data.data;
          })
          .catch( (error)=> {
            console.log(error);
          });
      }
    },
    //获取指定类型的歌手
    getSingerList (id) {
      let that = this;
      HttpManager.getSingerList({ category: id, pn: 1, rn: 6 })
        .then((res) => {
          // console.log(res.data);
          that.artistData = Object.assign({}, that.artistData,
            {
              code: parseInt(res.code),
              total: 0,
              artistList: res.data.artistList,
              pageSize: 6,
              getData: null,
            })
          // that.dataList = res.data.artistList;
        })
        .catch( (error)=> {
          console.log(error);
        });
    },
    //获取轮播图
    getSwiperList () {
      let that = this;
      HttpManager.getSwiperList()
        .then((res) => {
          // console.log(res.data);
          that.swiperList = res.data;
          that.swiperList.shift();//去除数组的第一个元素
        })
        .catch( (error)=> {
          console.log(error);
        });
    },

    //获取轮播图的宽度
    getSwiperWidth () {
      //swiperWidth的最小值为750px,最大值1260px，padding为120px
      //swiperWidth+padding*2 = innerWidth
      if (window.innerWidth >= 990 && window.innerWidth <= 1520) {
        this.swiperWidth = window.innerWidth - 120 * 2;
      }
    },
    //设置轮播图高度
    setSwiperHeight () {
      this.getSwiperWidth();
      this.swiperHeight = 340 / 1400 * this.swiperWidth;
    },
    // imgError (event) {
    //   event.currentTarget.src = "https://h5static.kuwo.cn/upload/image/4f768883f75b17a426c95b93692d98bec7d3ee9240f77f5ea68fc63870fdb050.png";
    //   event.currentTarget.onerror = null;
    // }
    throttle (func, wait) {
      let previous = 0;
      return function () {
        let now = Date.now();
        let context = this;
        let args = arguments;
        if (now - previous > wait) {
          func.apply(context, args);
          previous = now;
        }
      }
    }


  },

  computed: {},
  created () {
    this.setSwiperHeight();
    this.getHomeData();
  },
  mounted () {
    // 100ms运行1次
    window.onresize = this.throttle(this.setSwiperHeight, 100)
    // () => {//监听窗口resize事件
    //   this.setSwiperHeight();
    // }
  },
  beforeDestroy () {//注意：监听的函数要在关闭页面时销毁掉,不然会影响到其它页面
    window.onresize = null;
  },
}

</script>
<style lang='scss' scoped>
@import "../assets/css/home";
@import "../assets/css/font";
.rec_nav {
  display: flex;
  align-items: baseline;
  margin-top: 30px;
  margin-bottom: 25px;
  .title {
    font-size: 28px;
    font-weight: 600;
    line-height: 30px;
    margin-right: 40px;
  }
  a {
    position: relative;
    display: inline-block;
    height: 20px;
    line-height: 20px;
    margin-right: 34px;
    font-size: 15px;
    font-weight: 300;
    color: #444;
    text-decoration: none;
    transition: none;
    &.active {
      font-weight: 600;
      color: #000;
      &:hover {
        color: #000;
      }
      &:after {
        position: absolute;
        content: "";
        left: 0;
        bottom: 1px;
        width: 100%;
        height: 6px;
        background: #31c27c;
        z-index: -1;
      }
    }

    &:hover {
      color: #31c27c;
    }
  }
  .more {
    position: relative;
    color: #999;
    font-size: 13px;
    font-weight: 400;
    height: 20px;
    line-height: 20px;
    display: flex;
  }
}
.rec_list {
  display: flex;
  flex-wrap: wrap;
  .bang {
    width: 18%;
    box-shadow: 0 0 30px 0 rgba(65, 67, 70, 0.08);
    margin-left: 1%;
    margin-right: 1%;
    li {
      list-style: none;
    }
    .bang_top {
      text-align: center;
      // padding: 13px 16px 13px;

      background-color: blanchedalmond;
      height: 50px;
      line-height: 50px;
      font-weight: 600;
      // cursor: pointer;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .list_con {
      padding: 18px 15px 26px;
      li {
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;
      }
      .top_img {
        width: 20px;
        height: 33px;
      }
      .top1 {
        background: url(https://h5static.kuwo.cn/www/kw-www/img/icon_first.df06fb1.png)
          center 4px no-repeat;
        background-size: 168%;
      }

      .top2 {
        background: url(https://h5static.kuwo.cn/www/kw-www/img/icon_secondary.d99e73e.png)
          center 4px no-repeat;
        background-size: 168%;
      }

      .top3 {
        background: url(https://h5static.kuwo.cn/www/kw-www/img/icon_third.8abbb65.png)
          center 4px no-repeat;
        background-size: 168%;
      }
      .index {
        display: inline-block;
        font-weight: 600;
        font-size: 16px;
        width: 20px;
        text-align: center;
      }
      .info {
        width: 80%;
        padding-left: 10px;
        .song_name {
          width: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-size: 15px;
          font-weight: 400;
          height: 22px;
          line-height: 22px;
          cursor: pointer;
          &:hover {
            font-weight: 600;
          }
        }
        .artist {
          width: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          margin-top: 4px;
          font-size: 13px;
          font-weight: 300;
          color: #999;
          height: 20px;
          line-height: 20px;
          cursor: pointer;
          &:hover {
            color: #000;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1200px) {
  .bang {
    width: 18%;
  }
}
.playlist_item {
  width: 18%;
  margin-right: 2%;
  .pic_out {
    position: relative;
    overflow: hidden;
    width: 100%;
    padding-bottom: 100%;
    .cover_play {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      background: transparent;
      z-index: 3;
      transition: all 0.2s ease-in-out;
      .play {
        position: absolute;
        top: 50%;
        left: 50%;
        background: #fff;
        border-radius: 50%;
        height: 50px;
        width: 50px;
        line-height: 50px;
        text-align: center;
        transform: translate(-50%, -50%);
        transition: all 0.2s ease-in-out;
        display: none;
      }
    }
    &:hover {
      .cover_play {
        background-color: rgba(5, 5, 5, 0.3);
      }
      .play {
        display: block;
      }
      .play .iconfont {
        font-size: 24px;
        color: #333;
      }
      .pic {
        // 等比缩放
        transform: scale(110%);
      }
    }
    .pic {
      position: absolute;
      z-index: 0;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      transition: transform 0.75s cubic-bezier(0, 1, 0.75, 1);
    }
  }
  .name {
    margin-top: 10px;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-break: break-all;
    span {
      cursor: pointer;
    }
  }
  .count {
    color: #999;
    margin-top: 5px;
    margin-bottom: 26px;
    font-size: 14px;
    line-height: 1;
    font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
      sans-serif;
  }
}
//歌手推荐
.artist_item {
  text-align: center;
  width: 14.6%;
  margin-left: 1%;
  margin-right: 1%;
  .pic_out {
    position: relative;
    width: 100%;
    padding-bottom: 100%;
    overflow: hidden;
    border-radius: 50%;
    .pic {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      transition: transform 1s cubic-bezier(0, 1, 1, 1);
    }
    &:hover {
      .pic {
        // 等比缩放
        transform: scale(110%);
      }
    }
  }
  .name {
    position: relative;
    height: 22px;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    margin-top: 10px;
    flex-wrap: nowrap;
    justify-content: center;
    padding: 0 5px;
    .text {
      text-overflow: ellipsis;
      white-space: nowrap;
      cursor: pointer;
      overflow: hidden;
    }
  }
  .num {
    margin-top: 5px;
    font-size: 13px;
    color: #999;
  }
}

.flex_c {
  display: flex;
  align-items: center;
}
</style>
<style lang='scss'>
.index_singer {
  .item {
    width: 13.32% !important;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 30px;
  }
}
</style>



